
/**
 * vuex管理登陆状态，具体可以参考官方登陆模板示例
 */
import { mapMutations } from "vuex";

export default {
  methods: {
    ...mapMutations(["login","getMsg","setHistory","setId","setClip"]),
  },
  onLaunch: function() {
    let userInfo = uni.getStorageSync("userInfo") || "";
    let userMsg = uni.getStorageSync("userMsg" || "");
    let searchHistory = uni.getStorageSync("searchHistory" || "");
    let clipText = uni.getStorageSync("clipText" || "")
    if (userInfo.uid) {
      //更新登陆状态
      uni.getStorage({
        key: "userInfo",
        success: res => {
          this.login(res.data);
        }
      });
      //持久化用户信息
      uni.getStorage({
        key: "userMsg",
        success: res => {
          this.getMsg(res.data);
        }
      });
    }
    // 保存历史搜索
    uni.getStorage({
      key: "searchHistory",
      success: res => {
        this.setHistory(res.data);
      }
    });

    uni.getStorage({
      key: "cateId",
      success: res => {
        this.setId(res.data);
      }
    });

    uni.getStorage({
      key: "clipText",
      success: res => {
        this.setClip(res.data);
      }
    });
  },

  onShow: function() {
    // console.log("App Show");
    uni.getNetworkType({  //检测网络
        success: function (res) {
            console.log(res.networkType);
        }
    });
    uni.onNetworkStatusChange(function (res) {  //检测网络更改
        console.log(res.isConnected);
        console.log(res.networkType);
    });
  },
  onHide: function() {
    console.log("App Hide");
  }
};
